/**
 *  Define the blog styles.
 *
 */

.post-container {
  padding: 0 1.5em;

  @include min($desktop) {
    padding: 0 3.5em;
  }
}

.blog-post-wrapper {
  position: relative;
  margin-top: 1em;
  padding: 0 0 6em;
  background: $off-white;

  .container {
    @include min($large) {
      width: 1270px;
    }
  }

  .col {
    padding: 0;
  }
}

.blog-hero__image {
  display: inline-block;
  height: 520px;
  width: 100%;
  border: 14px solid $white;
  border-bottom: 44px solid $white;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: 0 0 1px rgba($black, .3);

  &.blog-hero__image--1 {
    backface-visibility: hidden;
    transform: rotate(-1deg);
  }

  &.blog-hero__image--2 {
    margin-top: 2em;
    margin-left: -70px;
  }
}

.blog-post {
  position: relative;
  z-index: 20;
  margin-top: -80px;
  padding: 2em 0 0;
  background: $white;
  color: $black;
  box-shadow: 0 1px 4px rgba($black, 0.4);

  p {
    max-width: 700px;
    margin-bottom: 1em;
    line-height: 1.8;
    font-size: 1.8rem;

    &.post__lead {
      border-top: 3px solid $black;
      padding-top: 24px;
      font-style: italic;
    }
  }

  .inline-images {
    margin: 2.5em 0 0;
  }

  img {
    width: 100%;
    height: auto;
  }
}

.blog-hero {
  padding-top: 3em;
}

.blog-post__title {
  max-width: 700px;
  margin-top: 0;
  margin-bottom: 0.6em;
  text-align: left;
  letter-spacing: 1px;
  line-height: 1.2;
  font-family: $sans;
  font-size: 4rem;
  font-weight: 400;
}

.blog-post__date {
  padding: 1em 0 0.5em;
  display: inline-block;
  margin-bottom: 1em;
  color: $black;
  letter-spacing: 3px;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: bold;
}

.blog-post-image {
  position: relative;
  z-index: 10;
  height: 600px;
  border-right: 7px solid $white;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  backface-visibility: hidden;

  @include min($desktop) {
    min-height: 600px;
    height: 88vh;
  }
}

.callout--blog {
  margin-top: 1em;
  color: $white;
}

